// --- MEDIA TOOL (VIDEO/AUDIO) ---
class LiveMediaTool implements BlockTool {
  static get toolbox() { return { title: 'Video/Audio', icon: 'ðŸŽ¥' }; }
  private data: any;
  private wrapper: HTMLElement;
  private descriptionField: HTMLElement | null = null;
  constructor({ data }: { data: any }) {
    this.data = data || { url: '', type: '', name: '', description: '' };
    this.wrapper = document.createElement('div');
  }
  render() {
    this.wrapper.style.padding = "15px";
    this.wrapper.style.border = "1px solid #f0f0f0";
    this.wrapper.style.borderRadius = "10px";
    this.wrapper.style.background = "#fafafa";
    if (this.data && this.data.url) { this._renderPlayer(this.data.url, this.data.type); } 
    else {
      const input = document.createElement('input');
      input.type = 'file';
      input.accept = 'video/*,audio/*';
      input.style.display = 'none';
      input.onchange = (e: any) => {
        const file = e.target.files[0];
        if (!file) return;
        const reader = new FileReader();
        reader.onload = (event) => {
          const url = event.target?.result as string;
          const type = file.type.startsWith('video') ? 'video' : 'audio';
          this.data = { ...this.data, url, type, name: file.name };
          this._renderPlayer(url, type);
        };
        reader.readAsDataURL(file);
      };
      this.wrapper.appendChild(input);
      setTimeout(() => input.click(), 0);
    }
    return this.wrapper;
  }
  _renderPlayer(url: string, type: 'video' | 'audio') {
    this.wrapper.innerHTML = '';
    const mediaTag = document.createElement(type);
    mediaTag.src = url; mediaTag.controls = true; mediaTag.style.width = "100%";
    const desc = document.createElement('div');
    desc.contentEditable = "true";
    desc.dataset.placeholder = "Add a description...";
    desc.innerHTML = this.data.description || '';
    desc.style.cssText = "margin-top:10px; padding:8px; border-left:2px solid #0070f3; background: #fff;";
    desc.addEventListener('input', () => { this.data.description = desc.innerHTML; });
    this.descriptionField = desc;
    this.wrapper.appendChild(mediaTag);
    this.wrapper.appendChild(desc);
  }
  save() { return { ...this.data, description: this.descriptionField ? this.descriptionField.innerHTML : this.data.description }; }
}

// --- CONVERTER ---
const convertToMarkdown = (data: OutputData): string => {
  let markdown = ""
  data.blocks.forEach((block) => {
    // Handle Custom H1-H6
    if (block.type.match(/^h[1-6]$/)) {
        const level = block.type.charAt(1);
        markdown += `${"#".repeat(parseInt(level))} ${block.data.text}\n\n`
    } else {
        switch (block.type) {
          case "header":
            markdown += `${"#".repeat(block.data.level || 2)} ${block.data.text}\n\n`
            break
          case "paragraph":
            markdown += `${block.data.text}\n\n`
            break
          case "quote":
            markdown += `> ${block.data.text}\n`
            if (block.data.caption) markdown += `> â€” *${block.data.caption}*\n`
            markdown += `\n`
            break
          case "code":
            markdown += `\`\`\`\n${block.data.code}\n\`\`\`\n\n`
            break
          case "media":
            const tag = block.data.type === 'video' ? 'video' : 'audio';
            markdown += `<${tag} controls src="${block.data.url}" style="width:100%"></${tag}>\n`
            if (block.data.description) markdown += `\n*${block.data.description.replace(/<[^>]*>/g, '')}*\n\n`
            else markdown += `\n`
            break
          case "embed": // YouTube/Vimeo
            markdown += `<iframe width="100%" height="400" src="${block.data.embed}" frameborder="0" allowfullscreen></iframe>\n`
            if (block.data.caption) markdown += `\n*${block.data.caption}*\n\n`
            break
          case "delimiter":
            markdown += `---\n\n`
            break
          case "list":
            block.data.items.forEach((item: string, i: number) => {
              markdown += block.data.style === "ordered" ? `${i + 1}. ${item}\n` : `- ${item}\n`
            })
            markdown += "\n"
            break
          case "image":
            markdown += `![${block.data.caption || ""}](${block.data.file.url})\n\n`
            break
          default:
            if (block.data.text) markdown += `${block.data.text}\n\n`
            break
        }
    }
  })
  return markdown.trim()
}

export default function Page() {
  const editorInstance = useRef<EditorJS | null>(null)
  const [markdown, setMarkdown] = useState<string>("")
  const [showMarkdown, setShowMarkdown] = useState<boolean>(false)

  useEffect(() => {
    if (!editorInstance.current) initEditor()
    return () => { editorInstance.current?.destroy(); editorInstance.current = null; }
  }, [])

  const initEditor = () => {
    const editor = new EditorJS({
      holder: EDITOR_HOLDER_ID,
      placeholder: "Type '/' for commands...",
      onReady: () => { /* @ts-ignore */ new DragDrop(editor) },
      inlineToolbar: true, // Enable inline formatting (Bold, Italic, etc)
      tools: {
        h1: { 
          class: Header, 
          toolbox: { title: 'Heading 1', icon: '<b>H1</b>' },
          config: { placeholder: 'Heading 1', levels: [1], defaultLevel: 1 } 
        },
        h2: { 
          class: Header, 
          toolbox: { title: 'Heading 2', icon: '<b>H2</b>' },
          config: { placeholder: 'Heading 2', levels: [2], defaultLevel: 2 } 
        },
        h3: { 
          class: Header, 
          toolbox: { title: 'Heading 3', icon: '<b>H3</b>' },
          config: { placeholder: 'Heading 3', levels: [3], defaultLevel: 3 } 
        },
        paragraph: {
          config: { preserveBlank: true }
        },
        list: {
          class: List,
          inlineToolbar: true,
          config: { defaultStyle: 'unordered' }
        },
        youtube: {
          class: Embed,
          toolbox: { title: 'YouTube', icon: 'ðŸ“º' },
          config: {
            services: {
              youtube: true
            }
          }
        },
        media: LiveMediaTool,
        image: {
          class: ImageTool,
          config: {
            uploader: {
              uploadByFile: (file: File) => {
                return new Promise((res) => {
                  const reader = new FileReader();
                  reader.onload = (e) => res({ success: 1, file: { url: e.target?.result } });
                  reader.readAsDataURL(file);
                });
              }
            }
          }
        },
        quote: {
          class: Quote,
          inlineToolbar: true,
          toolbox: { title: 'Quote' },
          config: { quotePlaceholder: 'Enter a quote', captionPlaceholder: 'Author' }
        },
        code: Code,
        delimiter: {
          class: Delimiter,
          toolbox: { title: 'Divider Line', icon: 'â€•' }
        },
        // --- INLINE FORMATTING TOOLS ---
        underline: Underline,
        strikethrough: Strikethrough,
        superscript: Superscript,
        subscript: Subscript,
        inlineCode: InlineCode,
        marker: Marker,
      }
    })
    editorInstance.current = editor
  }

  const handleSave = async () => {
    const data = await editorInstance.current?.save()
    if (data) {
      setMarkdown(convertToMarkdown(data))
      setShowMarkdown(true)
    }
  }

  return (
    <div style={{ padding: "40px", maxWidth: "850px", margin: "0 auto", fontFamily: "system-ui" }}>
      <style>{`
        .ce-delimiter { line-height: 1.6em; width: 100%; text-align: center; margin: 20px 0; }
        .ce-delimiter:before { content: ""; display: inline-block; width: 100%; height: 1px; background-color: #e0e0e0; vertical-align: middle; }
        .ce-block__content, .ce-toolbar__content { max-width: 100% !important; }
        .cdx-button { cursor: pointer; }
      `}</style>

      <div id={EDITOR_HOLDER_ID} style={{ border: "1px solid #e0e0e0", padding: "30px", borderRadius: "16px", minHeight: "500px", backgroundColor: "#fff" }} />
      
      <div style={{ display: "flex", justifyContent: "center", marginTop: "30px" }}>
        <button onClick={handleSave} style={{ padding: "14px 32px", background: "#0070f3", color: "white", borderRadius: "10px", border: "none", cursor: "pointer", fontWeight: "600" }}>
          Generate CMS Output
        </button>
      </div>

      {showMarkdown && (
        <div style={{ marginTop: "50px" }}>
          <h3 style={{ color: "#444" }}>Visual Preview:</h3>
          <div 
            style={{ border: "1px solid #ddd", padding: "40px", borderRadius: "16px", background: "#fff" }} 
            dangerouslySetInnerHTML={{ __html: markdown.replace(/\n/g, '<br/>') }} 
          />
          <h3 style={{ marginTop: "30px", color: "#444" }}>Markdown / HTML Export:</h3>
          <pre style={{ background: "#1a1a1a", color: "#80cbc4", padding: "25px", borderRadius: "12px", overflowX: "auto" }}>
            <code>{markdown}</code>
          </pre>
        </div>
      )}
    </div>
  )
}